
body {
  font-family: sans-serif;
  font-size: 12px;
  background: #eee;
}
html {
  background: #eee;
}
* {
  margin: 0;
  padding: 0;
}
ul, li {
  list-style: none;
}
img {
  border-style: none;
  width: 100%;
  height: 100%;
}
audio {
  display: block;
}
a {
  text-decoration: none;
  color: #747474;
}
audio:not([controls]) {
  display: block;
}
input {
  border-style: none;
  border-radius: .1rem;
  padding: 0;
}
input:hover {
    border-style: none;
}
button, input, select, textarea {
  font-size: 100%;
  outline: 0;
}
button, input {
  overflow: visible;
}
input, textarea, button, a {
  -webkit-tap-highlight-color: transparent;
}
@media screen and (min-width: 414px) {
html {
    font-size: 55.2px;
}
}
@media screen and (min-width: 375px) and (max-width: 413px) {
html {
    font-size: 50px;
}
}
@media screen and (min-width: 320px) and (max-width: 374px) {
html {
    font-size: 42.6667px;
}
}
